import React from "react";
import { Modal, Button } from "react-bootstrap";
import "./notifications-modal.scss";
import { connect } from "react-redux";
import { setEditContributorData } from "../../redux/table/table-actions";
import axios from "axios";
import { IRequest } from "../navbar/custom-navbar";
import { setRequests } from "../../redux/requests/requests-actions";
import { Toast } from "./../toaster/toaser";

interface NotificationsModalProps {
  show: boolean;
  setShow: (show: boolean) => void;
  requests: IRequest[];
  selectedRequest: number;
}

const NotificationsModal = ({
  show,
  setShow,
  requests,
  selectedRequest,
}: NotificationsModalProps) => {
  const handleClickButton = (e: any, res: string) => {
    e.stopPropagation();
    axios
      .post("/handleRequest", {
        id: requests[selectedRequest].id,
        isAccepted: res === "قبول",
      })
      .then(() => {
        setRequests(
          requests.filter((req) => requests[selectedRequest].id !== req.id)
        );
        Toast(`تم ${res} ${requests[selectedRequest].name}`, true);
      })
      .catch(() => Toast(`حدث خطأ ما، الرجاء المحاولة مرة أخرى`, false));
    setShow(false);
  };

  return (
    <Modal
      show={show}
      onHide={() => setShow(false)}
      backdrop="static"
      keyboard={false}
    >
      <Modal.Header closeButton />
      <div className="card-elements">
        {/* <label className="sr-only" htmlFor="inlineFormInputGroupUsername">
          Username
        </label> */}
        <div className="input-group">
          <div className="input-group-prepend">
            <div className="input-group-text">الاسم</div>
          </div>
          <span className="form-control">{requests[selectedRequest].name}</span>
        </div>
        <div className="input-group">
          <div className="input-group-prepend">
            <div className="input-group-text">البريد الإلكتروني</div>
          </div>
          <span className="form-control">
            {requests[selectedRequest].email}
          </span>
        </div>
        <div className="input-group">
          <div className="input-group-prepend">
            <div className="input-group-text">رقم المحمول</div>
          </div>
          <span className="form-control">
            {requests[selectedRequest].mobnum}
          </span>
        </div>
        <div className="input-group">
          <div className="input-group-prepend">
            <div className="input-group-text">تاريخ الميلاد</div>
          </div>
          <span className="form-control">
            {String(requests[selectedRequest]["date of birth"]).split("T")[0]}
          </span>
        </div>

        <div className="input-group">
          <div className="input-group-prepend">
            <div className="input-group-text">النوع</div>
          </div>
          <span className="form-control">
            {requests[selectedRequest]["gender"] ? "ذكر" : "أنثى"}
          </span>
        </div>
      </div>
      <Modal.Footer>
        <Button onClick={(e) => handleClickButton(e, "رفض")} variant="danger">
          رفض
        </Button>
        <Button onClick={(e) => handleClickButton(e, "قبول")} variant="success">
          قبول
        </Button>
      </Modal.Footer>
    </Modal>
  );
};

const mapDispatchToProps = (dispatch: any) => ({
  setRequests: (requests: any) => dispatch(setRequests(requests)),
});

const mapStateToProps = (state: any) => ({
  requests: state.requests.requests,
  selectedRequest: state.requests.selectedRequest,
});

export default connect(mapStateToProps, mapDispatchToProps)(NotificationsModal);
