import React, { useEffect } from "react";
import {
  Modal,
  Button,
  InputGroup,
  FormControl,
  Col,
  Form,
  Row,
} from "react-bootstrap";
import "../edit-and-add-modal/edit-and-add-modal.scss";
import { useDispatch, useSelector } from "react-redux";
import {
  setShowAdd,
  setEditContributorData,
  setTableContent,
  resetEditContributorData,
} from "./../../redux/table/table-actions";
import editAndAddInputsData from "./../edit-and-add-modal/editAndAddInputsData";
import axios from "axios";
import { IContributor } from "./../custom-table/utils";
import { Toast } from "./../toaster/toaser";

interface AddModalProps {}

const AddModal = ({}: AddModalProps) => {
  const dispatch = useDispatch<any>();
  const show = useSelector((state: any) => state.table.showAdd);
  const addContributorData = useSelector(
    (state: any) => state.table.editContributorData
  );
  const tableContent = useSelector((state: any) => state.table.tableContent);

  const editAndAddInputs = editAndAddInputsData(addContributorData);

  useEffect(() => dispatch(setEditContributorData({ Type: true })), []);
  useEffect(() => {
    const date = document.querySelectorAll("input")[3];
    if (
      String(addContributorData["Civil Number"]).length !== 12 &&
      date?.name == "Civil Number"
    ) {
      date?.setCustomValidity("من فضلك أدخل الرقم المدني بشكل صحيح");
    } else {
      date?.setCustomValidity("");
    }
  }, [addContributorData]);

  const handleClickSubmit = (e: any) => {
    e.preventDefault();
    axios
      .post("/createContributor", addContributorData)
      .then(() => {
        Toast("تم إضافة " + addContributorData.Name, true);
        dispatch(setTableContent([...tableContent, addContributorData]));
      })
      .catch(() => Toast("حدث خطأ ما ، الرجاء المحاولة مرة أخرى", false));
    dispatch(setEditContributorData({ Type: true }));
    dispatch(setShowAdd(false));
  };

  const handleChangeGender = (gender: boolean) => {
    dispatch(setEditContributorData({ ...addContributorData, Type: gender }));
  };

  return (
    <Modal
      show={show}
      onHide={() => dispatch(setShowAdd(false))}
      backdrop="static"
      keyboard={false}
    >
      <Modal.Header
        style={{ display: "flex", alignItems: "center" }}
        closeButton
      >
        <Modal.Title>إضافة</Modal.Title>
      </Modal.Header>
      <Form onSubmit={handleClickSubmit} className="forget-password-page3-form">
        {editAndAddInputs.map(
          ({ nameInput, type, value, name, ...props }, i) => (
            <InputGroup size="sm" className="mb-1 pl-3 pr-3 pt-2" key={i}>
              <InputGroup.Prepend>
                <InputGroup.Text
                  // style={{ width: "90px" }}
                  id="inputGroup-sizing-sm"
                >
                  {nameInput}
                </InputGroup.Text>
              </InputGroup.Prepend>
              <FormControl
                value={value}
                name={name}
                onChange={({ target: { value, name } }) =>
                  dispatch(
                    setEditContributorData({
                      ...addContributorData,
                      [name]: value,
                    })
                  )
                }
                {...props}
                type={type}
                aria-label="Small"
                aria-describedby="inputGroup-sizing-sm"
                autoComplete="off"
                required
              />
            </InputGroup>
          )
        )}
        <fieldset>
          <Form.Group as={Row}>
            <Col className="radio-group">
              <div className="female-male">
                <Form.Check
                  type="radio"
                  name="male"
                  id="maleRadio"
                  checked={addContributorData.Type}
                  onChange={() => handleChangeGender(true)}
                />
                <Form.Label as="legend" column>
                  ذكر
                </Form.Label>
              </div>
              <div className="female-male">
                <Form.Check
                  type="radio"
                  name="female"
                  id="femaleRadio"
                  checked={!addContributorData.Type}
                  onChange={() => handleChangeGender(false)}
                />
                <Form.Label as="legend" column>
                  انثى
                </Form.Label>
              </div>
            </Col>
          </Form.Group>
        </fieldset>
        <Modal.Footer>
          <Button
            variant="secondary"
            onClick={() => dispatch(setShowAdd(false))}
          >
            الغاء
          </Button>
          <Button type="submit" variant="success">
            إضافة
          </Button>
        </Modal.Footer>
      </Form>
    </Modal>
  );
};

export default AddModal;
