import React, { useState, useEffect } from "react";
import {
  Button,
  Form,
  FormControl,
  Nav,
  Navbar,
  NavDropdown,
} from "react-bootstrap";
import WcIcon from "@material-ui/icons/Wc";
import NotificationsIcon from "@material-ui/icons/Notifications";
import PersonAdd from "@material-ui/icons/PersonAdd";
import { Badge } from "@material-ui/core";
import { ReactComponent as Men } from "../../assets/icons/Men.svg";
import { ReactComponent as Women } from "../../assets/icons/Women.svg";
import { ReactComponent as True } from "../../assets/icons/True.svg";
import { ReactComponent as False } from "../../assets/icons/False.svg";
import "./custom-navbar.scss";
import NotificationsModal from "../notificationsIcon-modal/notifications-modal";
import { connect, useDispatch, useSelector } from "react-redux";
import {
  setQuery,
  setSelectedPage,
  setShowAdd,
  setGenderFilter,
} from "./../../redux/table/table-actions";
import {
  setRequests,
  setSelectedRequest,
} from "./../../redux/requests/requests-actions";
import axios from "axios";
import AddModal from "./../add-modal/add-modal";
import { Link, useLocation } from "react-router-dom";
import { Toast } from "./../toaster/toaser";
import { useCookies } from "react-cookie";

export interface IRequest {
  id: number;
  name: string;
  email: string;
  "date of birth": string;
  gender: boolean;
  mobnum: string;
  active: boolean;
}

interface NavbarProps {
  setQuery: (query: string) => void;
  setSelectedPage: (page: number) => void;
  setRequests: ([]: any) => void;
  setSelectedRequest: (requestIdx: number) => void;
  requests: IRequest[];
  setShowAdd: (state: boolean) => void;
}

const CustomNavbar = ({
  setQuery,
  setSelectedPage,
  setRequests,
  setSelectedRequest,
  requests,
  setShowAdd,
}: NavbarProps) => {
  const [notification, setNotificationsModal] = useState(false);
  const genderFilter = useSelector<any>((state) => state.table.genderFilter);
  const [cookies] = useCookies(["userdata"]);

  const handleClickButton = (e: any, id: number, res: string) => {
    e.stopPropagation();
    axios
      .post("/handleEnDisUserAcc", {
        id,
        status: res === "قبول",
      })
      .then(() => {
        // setRequests(requests.filter((req) => id !== req.id));
        setRequests(
          requests.map((usr) =>
            usr.id !== id
              ? usr
              : {
                  ...usr,
                  status: res === "قبول",
                  active: res === "قبول" ? true : usr.active,
                }
          )
        );
        Toast(
          `تم ${res} ${
            requests[requests.findIndex((req) => req.id === id)].name
          }`,
          true
        );
      })
      .catch(() => Toast(`حدث خطأ ما، الرجاء المحاولة مرة أخرى`, false));
  };

  const handleChangeSearch = ({ target: { value } }: any) => {
    setQuery(value);
    setSelectedPage(1);
  };

  let [notAccepted, setNotAccepted] = React.useState<IRequest[]>([]);
  useEffect(() => {
    if (cookies["userdata"].name === "admin")
      axios.post("/listPendingRequests").then(({ data }) => setRequests(data));
  }, []);
  useEffect(() => {
    setNotAccepted(requests.filter((req) => !req.active));
    console.log("notAccepted", notAccepted);
  }, [requests]);

  const dispatch = useDispatch();
  const location = useLocation();
  return (
    <Navbar
      className="navbar"
      collapseOnSelect
      expand="lg"
      bg="dark  "
      variant="dark"
    >
      <Link to="/">
        <Navbar.Brand>
          <h1 style={{ fontWeight: "bold", fontFamily: "GE", marginBottom: 0 }}>
            أحمد <span className="text-warning">الأصفر</span>
          </h1>
        </Navbar.Brand>
      </Link>

      <Navbar.Toggle aria-controls="responsive-navbar-nav" />
      <Navbar.Collapse id="responsive-navbar-nav">
        <Nav className="mr-auto">
          <Form inline>
            <FormControl
              type="text"
              placeholder="بحث..."
              className="w-100 search-box"
              onChange={handleChangeSearch}
            />
          </Form>
          {cookies["userdata"].name === "admin" && (
            <>
              {location.pathname === "/table" && (
                <Nav.Link className="nav-item" onClick={() => setShowAdd(true)}>
                  <div className="nav-item">
                    <PersonAdd />
                    <span className="header-item-title">إضافة عضو </span>
                  </div>
                </Nav.Link>
              )}
              <NavDropdown
                className="nav-item-container"
                title={
                  <div className="nav-item">
                    <Badge badgeContent={notAccepted.length} color="secondary">
                      <NotificationsIcon />
                    </Badge>
                    <span className="header-item-title">طلبات الإنضمام </span>
                  </div>
                }
                id="notifications-nav-dropdown"
              >
                {!requests.filter((req) => !req.active).length ? (
                  <div className="requests-not-found">
                    {/* <NavDropdown.Item className="notifications-person-name"> */}
                    لا توجد طلبات
                    {/* </NavDropdown.Item> */}
                  </div>
                ) : (
                  notAccepted.map(({ name, id }, i) => (
                    <>
                      <div
                        className="notifications-dropdown"
                        onClick={() => {
                          setSelectedRequest(i);
                          setNotificationsModal(true);
                        }}
                      >
                        <NavDropdown.Item className="notifications-person-name">
                          <span className="request-name">{name}</span>
                          <div className="requests-buttons">
                            <True
                              className="accept-and-reject-button"
                              onClick={(e) => handleClickButton(e, id, "قبول")}
                              width={25}
                              height={25}
                            />
                            <False
                              className="accept-and-reject-button"
                              onClick={(e) => handleClickButton(e, id, "قبول")}
                              width={25}
                              height={25}
                            />
                          </div>
                        </NavDropdown.Item>
                      </div>
                      <NavDropdown.Divider />
                    </>
                  ))
                )}
              </NavDropdown>
            </>
          )}

          <NavDropdown
            className="nav-item"
            title={
              <>
                {typeof genderFilter !== "boolean" && (
                  <div className="nav-item">
                    <WcIcon />
                    <span className="header-item-title">كلا النوعين </span>
                  </div>
                )}
                {genderFilter === true && (
                  <div className="nav-item">
                    <Men fill="#9A9DA0" width={24} height={24} />
                    <span className="header-item-title">ذكر </span>
                  </div>
                )}
                {genderFilter === false && (
                  <div className="nav-item">
                    <Women fill="#9A9DA0" width={24} height={24} />
                    <span className="header-item-title">أنثى </span>
                  </div>
                )}
              </>
            }
            id="gender-nav-dropdown"
          >
            <NavDropdown.Item
              onClick={() => dispatch(setGenderFilter(null))}
              style={{ display: "flex", justifyContent: "space-between" }}
              active={typeof genderFilter !== "boolean"}
            >
              <span className="genderOptions">كلا النوعين</span>
              <WcIcon />
            </NavDropdown.Item>

            <NavDropdown.Item
              onClick={() => dispatch(setGenderFilter(true))}
              style={{ display: "flex", justifyContent: "space-between" }}
              active={genderFilter === true}
            >
              <span className="genderOptions">ذكر</span>
              <Men width={24} height={24} />
            </NavDropdown.Item>
            <NavDropdown.Item
              onClick={() => dispatch(setGenderFilter(false))}
              style={{ display: "flex", justifyContent: "space-between" }}
              active={genderFilter === false}
            >
              <span className="genderOptions">أنثى</span>
              <Women width={24} height={24} />
            </NavDropdown.Item>
          </NavDropdown>
        </Nav>
      </Navbar.Collapse>
      <AddModal />
      {notification && (
        <NotificationsModal
          show={notification}
          setShow={setNotificationsModal}
        />
      )}
    </Navbar>
  );
};

const mapDispatchToProps = (dispatch: any) => ({
  setQuery: (query: string) => dispatch(setQuery(query)),
  setSelectedPage: (page: number) => dispatch(setSelectedPage(page)),
  setRequests: (requests: any) => dispatch(setRequests(requests)),
  setSelectedRequest: (requestIdx: number) =>
    dispatch(setSelectedRequest(requestIdx)),
  setShowAdd: (state: boolean) => dispatch(setShowAdd(state)),
});

const mapStateToProps = (state: any) => ({
  requests: state.requests.requests,
  selectedRequest: state.requests.selectedRequest,
});

export default connect(mapStateToProps, mapDispatchToProps)(CustomNavbar);
