const editAndAddInputsData = (editContributorData: any) => [
  {
    nameInput: "الاسم",
    type: "name",
    name: "Name",
    value: editContributorData["Name"],
  },
  {
    nameInput: "العائلة",
    type: "lname",

    name: "Family",
    value: editContributorData["Family"],
  },
  {
    nameInput: "الرقم المدني",
    type: "number",
    maxLength: 12,
    pattern: "/^-?d+.?d*$/",
    name: "Civil Number",
    value: editContributorData["Civil Number"],
  },
  {
    nameInput: "تاريخ الميلاد",
    type: "date",
    name: "Date of Birth",
    value: editContributorData["Date of Birth"]?.split("T")[0],
  },
  {
    nameInput: "الصندوق",
    type: "number",
    name: "Box",
    value: editContributorData["Box"],
  },
  {
    nameInput: "المنطقة",
    type: "text",

    name: "Region",
    value: editContributorData["Region"],
  },
  {
    nameInput: "الشارع",
    type: "text",
    name: "Street",
    value: editContributorData["Street"],
  },
  {
    nameInput: "المنزل",
    type: "text",
    name: "House",
    value: editContributorData["House"],
  },
  {
    nameInput: "الرقم الآلي للعنوان",
    type: "number",
    name: "Automated Number",
    value: editContributorData["Automated Number"],
  },
];
export default editAndAddInputsData;
